import Vue from "vue";
import VueRouter from "vue-router";
const VueRouterPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push (to) {
    return VueRouterPush.call(this, to).catch(err => err)
};
import Home from "../views/Home.vue";
import homeNavBar from "../components/homecomponents/homeNavBar.vue"
import homePage from "../components/homecomponents/homeNavBarcomponents/homePage.vue"
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
	redirect:"/homeNavBar/homePage"
  },
  {
    path: "/about",
    name: "About",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  },
  //homeNavBar相关路由
  {
	path:"/homeNavBar",
	name:"homeNavBar",
	component:homeNavBar,
	children:[
		{
			path:"homePage",
			name:"homePage",
			component: homePage
		},
		{
			path:'spotDetail',
			name:'spotDetail',
			component:()=>import("../components/homecomponents/homeNavBarcomponents/spotDetail.vue")
		},
		{
			path:'miracle',
			name:'miracle',
			component:()=>import("../components/homecomponents/homeNavBarcomponents/miracle.vue")
		},
		{
			path:'foodDetail',
			name:'foodDetail',
			component:()=>import("../components/homecomponents/homeNavBarcomponents/foodDetail.vue")
		},
		{
			path:'delicious',
			name:'delicious',
			component:()=>import("../components/homecomponents/homeNavBarcomponents/delicious.vue")
		},
		{
			path:'spotSearch',
			name:'spotSearch',
			component:()=>import("../components/homecomponents/homeNavBarcomponents/homePagecomponents/searchcomponents/spotSearch.vue")
		},
		{
			path:'foodSearch',
			name:'foodSearch',
			component:()=>import("../components/homecomponents/homeNavBarcomponents/homePagecomponents/searchcomponents/foodSearch.vue")
		},
		{
			path:'fengGuang',
			name:'fengGuang',
			component:()=>import("../components/homecomponents/homeNavBarcomponents/fengGuang.vue")
		},
		{
			path:'renWen',
			name:'renWen',
			component:()=>import("../components/homecomponents/homeNavBarcomponents/renWen.vue")
		},
		{
			path:'shanHe',
			name:'shanHE',
			component:()=>import("../components/homecomponents/homeNavBarcomponents/shanHe.vue")
		},
		{
			path:'foreign',
			name:'foreign',
			component:()=>import("../components/homecomponents/homeNavBarcomponents/foreign.vue")
		},
		{
			path:'domestic',
			name:'domestic',
			component:()=>import("../components/homecomponents/homeNavBarcomponents/domestic.vue")
		}
	]
  }
];

const router = new VueRouter({
  routes
});

export default router;
