<template>
  <div id="">
    <search></search>
    <hot-spots :homeHotSpots="homeHotSpots"></hot-spots>
    <spe-spots :homeSpeSpots="homeSpeSpots"></spe-spots>
    <spe-foods :homeSpeFoods="homeSpeFoods"></spe-foods>
  </div>
</template>

<script>
import search from "./homePagecomponents/search.vue";
import hotSpots from "./homePagecomponents/hotSpots.vue";
import speSpots from "./homePagecomponents/speSpots.vue";
import speFoods from "./homePagecomponents/speFoods.vue";
import about from "../../../views/About.vue";
var AV = about.AV;
export default {
  name: "homePage",
  data() {
    return {
      homeHotSpots: null,
      homeSpeFoods: null,
      homeSpeSpots: null
    };
  },
  components: {
    search,
    hotSpots,
    speSpots,
    speFoods
  },
  mounted() {
    var query = new AV.Query("spots");
    var that = this;
    query.limit(8);
    query.equalTo("country", "中国");
    query.find().then(function(data) {
      that.homeHotSpots = JSON.parse(JSON.stringify(data));
    });
    var query1 = new AV.Query("foods");
    query1.equalTo("country", "中国");
    query1.limit(6);
    query1.find().then(function(data) {
      that.homeSpeFoods = JSON.parse(JSON.stringify(data));
    });
    var query2 = new AV.Query("spots");
    query2.limit(8);
    query2.find().then(function(data) {
      that.homeSpeSpots = JSON.parse(JSON.stringify(data));
    });
  }
};
</script>

<style></style>
