<template>
	<div id='search'>
		<div id='searchLine'></div>
		<div id='seacrhBox'>
			<input type="text" v-model="spot1" placeholder="搜景点" id="searchSceneryIpt" />
			<button id="searchScenery" @click="spot">搜索</button>
			<div id='searchWhiteLine'></div>
			<input type="text" v-model="food1" placeholder="搜美食" id="searchFoodIpt"/>
			<button id="searchFood" @click="food">搜索</button>
		</div>
		<div id='searchLine'></div>
	</div>
</template>

<script>
	export default{
		name:'search',
		data(){
			return{
				spot1:'',
				food1:''
			}
		},
		methods:{
			spot(){
				var that=this;
				this.$router.push({path:'/homeNavBar/spotSearch',query:{spot1:that.spot1}})
			},
			food(){
				var that=this;
				this.$router.push({path:'/homeNavBar/foodSearch',query:{food1:that.food1}})
			}
		}
	}
</script>

<style>
	#search{
		margin-top: 20px;
		width: 100%;
	}
	#searchLine{
		margin: 10px 0 10px 0;
		width: 90%;
		margin-left: 5%;
		height: 1px;
		background-color: #61CE3C;
	}
	#seacrhBox{
		height: 100px;
		width: 80%;
		background-color: #E6E6E6;
		display: flex;
		justify-content:center;
		align-items: center;
		margin-left: 10%;
	}
	#searchSceneryIpt,#searchScenery,#searchFoodIpt,#searchFood,#searchWhiteLine{
		height: 60px;
		margin-right: 20px;
		font-size: 20px;
		text-align: center;
		outline: none;
	}
	input::-webkit-input-placeholder {
	color: #999; 
	-webkit-transition: color.5s; 
	} 
	input:focus::-webkit-input-placeholder, input:hover::-webkit-input-placeholder { 
	color: #c2c2c2; 
	-webkit-transition: color.5s; 
	}
	#searchWhiteLine{
		width: 2px;
		height: 90px;
		background-color: white;
	}
	#searchSceneryIpt,#searchFoodIpt{
		border: 1px solid #E6E6E6;
		border-radius: 10px;
		width: 15%;
		color: #01DFA5;
	}
	#searchScenery,#searchFood{
		width: 10%;
		border: 1px solid #E6E6E6;
		border-radius: 10px;
		background-color: #61CE3C;
		color: white;
	}

</style>
