<template>
	<div id="speSpots">
		<div id="speSpotsWords"><p>天下奇观</p></div>
		<div id="speSpotsPanel" >
			<div id="speSpotsPanelItem" v-for="(item,index) in homeSpeSpots" :key='index' :style="{backgroundImage:'url(' + item.img1 + ')'}"  
			@mouseover="mouseOver(item.id)" @mouseleave="mouseLeave()" @click="clicked(index)">
				<div id="speSpotsPanelItemWord">{{item.name}}</div>
				<transition name="fade">
					<div id="speSpotsPanelItemWordHidden" v-if="sspiwhIsShow==item.id" >{{item.summary}}</div>					
				</transition>
			</div>
		</div>
	</div>
</template>

<script>
export default{
	name :"speSpots",
	props:["homeSpeSpots"],
	data(){
		return{
			sspiwhIsShow:false
		}
	},
	methods:{
		mouseOver(id){
			this.sspiwhIsShow=id;
		},
		mouseLeave(){
			this.sspiwhIsShow='';
		},
		clicked(index){
			this.$router.push({path:"/homeNavBar/spotDetail",query:{datas:this.homeSpeSpots[index]}});
		}
	}
}
</script>

<style>
	#speSpots,#speSpotsWords,#speSpotsPanel{
		display: flex;
		justify-content: center;
		align-items:center;
	}
	#speSpots{
		flex-direction: column;
		width: 100%;
	}
	#speSpotsWords{
		width: 85%;
		font-size: 20px;
		justify-content: left;
		color: #FF0000;
	}
	#speSpotsPanel{
		width: 90%;
		height: 48vw;
		flex-wrap: wrap;
		transition: all 0.3s;
	}
	#speSpotsPanelItem{
		width: 18%;
		height: 22vw;
		margin: 0 2% 0 0;
		position: relative;
		background-repeat: no-repeat;
		background-size: 100% 100%;
		display: flex;
		justify-content: flex-end;
		align-items: flex-start;
		flex-direction: column;
	}
	#speSpotsPanelItem:first-child,#speSpotsPanelItem:last-child{
		width: 38%;
	}
	#speSpotsPanelItem:nth-child(4n){
		margin: 0;
	}
	#speSpotsPanelItemWord{
		color: white;
		font-size: 25px;
		margin: 0 0 1vw 1vw;
	}
	#speSpotsPanelItemWordHidden{
		overflow: hidden;
		background-color: #848484;
		font-size: 18px;
		text-indent: 2em;
		text-align: justify;
		text-justify: inter-ideograph;
		color: white;
		width: 100%;
	}
	.fade-enter-active,.fade-leave-active{
		transition:all 0.6s linear;
	}
	.fade-enter,.fade-leave-to{
		opacity: 0;
		height: 0;
	}
	.fade-enter-to,.fade-leave{
		opacity: 0.8;
		height:7vw;
	}
	@media only screen and (max-width:1100px){
		#speSpotsPanel{
			height: 530px;
		}
		#speSpotsPanelItem{
			height: 244px;
		}
		#speSpotsPanelItemWord{
			margin: 0 0 11px 11px;
		}
	}
</style>
