import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

var moduleA={
	state: {
		  name : '',
		  password:''
	},
	mutations: {
		  setName(state,name){
			  state.name=name;
		  }
	},
	actions: {
		  acSetName(context,name){
			  context.commit('setName',name)
		  }
	},
	getters:{
		  getName(state){
			  return state.name;
		  }
	}
}
